<script>
export default {
    methods:{
        enter:function($event){
            $event.currentTarget.className="selected";
        },
        leave:function($event){
            $event.currentTarget.className="";
        },
        entercharactor:function($event){
            $event.currentTarget.className="charactor charactor-hover";
        },
        leavecharactor:function($event){
