    box-shadow: 3px 10px 30px 0 rgba(0,1,1,.10);
    transform: scale(1.1,1.1);
    z-index: 2;
    transition: all .3s cubic-bezier(.4,0,.2,1);
    position: relative;
    z-index: 2;

}
.services  .selected h2,.services  .selected span{
    color: #fff;
