	background: -moz-linear-gradient(10 10, 90% 100%, from(#239ece), to(#00adff)); /* Firefox 3.6 to 15 */
	background: linear-gradient(10 10, 90% 100%, from(#239ece), to(#00adff)); /*  */
	filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, startColorstr=#239ece, endColorstr=#00adff);/*IE<9>*/
	-ms-filter: "progid:DXImageTransform.Microsoft.gradient (GradientType=0, startColorstr=#239ece, endColorstr=#00adff)";/*IE8+*/
}

